@charset "UTF-8";

@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Lexend+Deca&display=swap");

* {
  border: 0;
  box-sizing: border-box;
  font-family: var(--primary-font), var(--neutral-font);
  list-style: none;
  margin: 0;
  outline: 0;
  padding: 0;
}

:root {
  --ebony-color: #090b1a;
  --medium-purple-color: #aa5cdb;
  --port-gore-color: #1b1938;
  --santas-gray-color: #a4a3af;
  --white-color: #ffffff;

  --neutral-font: sans-serif;
  --primary-font: "Inter";
  --secondary-font: "Lexend Deca";
}

html {
  align-items: center;
  display: flex;
  height: 100%;
  justify-content: center;
}

body {
  background-color: var(--ebony-color);
}
